/**
 * ------------------------------------------------------------------------
 * Data Api
 * ------------------------------------------------------------------------
 */
Utils.ready(() => {
  document.querySelectorAll(
    `${Constants.SELECTOR_DATA_TOGGLE}[id]`
  ).forEach((element) => {
    new Quicktab(element.getAttribute('id'))
  })

})

export default Quicktab
